@font-face {
  font-family: 'fontello';
  src: url('../fonts/fontello.eot?29655599');
  src: url('../fonts/fontello.eot?29655599#iefix') format('embedded-opentype'),
    url('../fonts/fontello.woff2?29655599') format('woff2'),
    url('../fonts/fontello.woff?29655599') format('woff'),
    url('../fonts/fontello.ttf?29655599') format('truetype'),
    url('../fonts/fontello.svg?29655599#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
